<template>
  <v-container>
    <div class="submit-form mt-3 mx-auto">
      <v-row>
        <v-col class="text-center" cols="12" md="8">
          <v-row class="pa-5">
            <h2>ADD CASEFILE</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/casefiles"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >CASEFILE LIST</v-btn
          >
        </v-col>
      </v-row>
      <div>
        <v-form ref="form" lazy-validation>
          <v-select
            v-model="casefile.dept"
            :items="dept"
            item-text="description"
            item-value="name"
            :menu-props="{ maxHeight: '400' }"
            label="DEPARTMENT"
            hint="Set the casefile department"
            persistent-hint
          ></v-select>

          <v-select
            v-model="casefile.year"
            :items="year"
            label="Year"
          ></v-select>

          <v-select
            v-model="casefile.branch"
            :items="branch"
            item-text="name"
            item-value="brCode"
            :menu-props="{ maxHeight: '400' }"
            label="BRANCH"
            hint="Set the casefile branch"
            persistent-hint
          ></v-select>

          <v-select
            v-model="casefile.insurer"
            :items="insurer"
            item-text="name"
            item-value="insCode"
            :menu-props="{ maxHeight: '400' }"
            label="INSURER"
            hint="Set the casefile insurer"
            persistent-hint
          ></v-select>

          <v-text-field
            v-model="casefile.claimNo"
            :rules="[(v) => !!v || 'Insurers reference is required']"
            label="Insurers Reference"
            required
          ></v-text-field>

          <v-text-field
            v-model="casefile.vehicleNo"
            :rules="[(v) => !!v || 'Vehicle Number is required']"
            label="Vehicle Number Reference"
            required
          ></v-text-field>

          <template>
            <div>
              <!-- <div class="mb-6">
                Active picker: <code>{{ activePicker || "null" }}</code>
              </div> -->
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="casefile.dateOfLoss"
                    label="Date of Loss"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="casefile.dateOfLoss"
                  :active-picker.sync="activePicker"
                  :max="
                    new Date(
                      Date.now() - new Date().getTimezoneOffset() * 60000
                    )
                      .toISOString()
                      .substr(0, 10)
                  "
                  min="1950-01-01"
                  @change="save"
                ></v-date-picker>
              </v-menu>
            </div>
          </template>

          <v-text-field
            v-model="casefile.placeOfLoss"
            :rules="[(v) => !!v || 'Place of Loss is required']"
            label="Place of Loss"
            required
          ></v-text-field>

          <v-text-field
            v-model="casefile.insuredName"
            :rules="[(v) => !!v || 'Insured Name is required']"
            label="Insured Name"
            required
          ></v-text-field>
          <v-text-field
            v-model="casefile.insComment"
            label="Insurers Comment"
          ></v-text-field>
          <!-- <v-text-field
            v-model="casefile.createdBy"
            label="Submited by"
          ></v-text-field> -->
          <!-- <v-text-field
            v-model="casefile.fileStatus"
            label="File Status"
          ></v-text-field> -->
        </v-form>

        <v-btn
          :disabled="isAddButtonDisabled"
          color="primary"
          class="mt-3"
          @click="saveCasefile"
          >Submit</v-btn
        >
      </div>
    </div>
  </v-container>
</template>

<script>
import BranchDataService from "../../services/BranchDataService";
import DeptDataService from "../../services/DeptDataService";
import InsDataService from "../../services/InsDataService";
import CasefileDataService from "../../services/CasefileDataService";

export default {
  name: "add-casefile",

  data() {
    return {
      activePicker: null,
      date: null,
      menu: false,

      branch: [],
      dept: [],
      insurer: [],
      year: ["2018", "2019", "2020", "2021", "2022"],
      casefile: {
        id: null,
        dept: "",
        year: "",
        branch: "",
        insurer: "",
        claimNo: "",
        vehicleNo: "",
        dateOfLoss: "",
        placeOfLoss: "",
        insuredName: "",
        insComment: "",
        createdBy: "",
        fileStatus: "NEW",
      },
    };
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.activePicker = "YEAR"));
    },
  },

  computed: {
    isAddButtonDisabled() {
      return !(
        this.casefile.dept &&
        this.casefile.year &&
        this.casefile.branch &&
        this.casefile.insurer &&
        this.casefile.claimNo &&
        this.casefile.vehicleNo &&
        this.casefile.dateOfLoss &&
        this.casefile.placeOfLoss &&
        this.casefile.insuredName &&
        this.casefile.insComment &&
        // this.casefile.createdBy &&
        this.casefile.fileStatus
      );
    },
  },

  methods: {
    retrieveBranch() {
      BranchDataService.getAll()
        .then((response) => {
          this.branch = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveDept() {
      DeptDataService.getAll()
        .then((response) => {
          this.dept = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveIns() {
      InsDataService.getAll()
        .then((response) => {
          this.insurer = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },

    save(date) {
      this.$refs.menu.save(date);
    },

    // refreshList() {
    //   this.retrieveAccess();
    //   this.retrieveBranch();
    // },

    saveCasefile() {
      var data = {
        dept: this.casefile.dept,
        year: this.casefile.year,
        branch: this.casefile.branch,
        insurer: this.casefile.insurer,
        claimNo: this.casefile.claimNo,
        vehicleNo: this.casefile.vehicleNo,
        dateOfLoss: this.casefile.dateOfLoss,
        placeOfLoss: this.casefile.placeOfLoss,
        insuredName: this.casefile.insuredName,
        insComment: this.casefile.insComment,
        createdBy: this.$store.state.auth.user.username,
        fileStatus: this.casefile.fileStatus,
      };

      CasefileDataService.create(data)
        .then((response) => {
          this.casefile.id = response.data.id;
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "casefiles" });
        })
        .catch((e) => {
          console.log(e);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (e.response && e.response.data && e.response.data.message) ||
              e.message ||
              toString(),
          });
        });
    },
  },
  mounted() {
    this.retrieveBranch();
    this.retrieveDept();
    this.retrieveIns();
  },
};
</script>
